// contrast-color
//
// @param {color} $color
// @param {color} $light Light default color
// @param {color} $dark Dark default color
// @return {color}
@function contrast-color($color, $dark: #000, $light: #fff) {
	@if (lightness($color) > 66) {
		@return $dark; // Lighter backgorund, return dark color
	} @else {
		@return $light; // Darker background, return light color
	}
}